@import url("https://fonts.googleapis.com/css2?family=Inter:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap");

.projectPage {
  color: white;
  height: 200vh;
}

.proTitle {
  width: 50vw;
  margin: 10vw 0 0 -3.3vw;
  text-align: right;
  font-family: "Inter", sans-serif;
  position: absolute;
  z-index: 10;
  background-color: transparent;
}

#pTitle {
  font-size: 4.5vw;
  background-color: transparent;
}
.smallTitle {
  text-align: left;
  margin-left: 10.7vw;
  font-size: 1.7vw;
  font-family: "Poppins", sans-serif;
  background-color: transparent;
}

.proImage {
  height: 32vw;
  width: 40vw;
  position: absolute;
  overflow: hidden;
  background-color: red;
  margin: 2vw 0 0 44.5vw;
  z-index: 1;
}

.proDesc {
  position: relative;
  margin: 40vw 0 3vw 5vw;
  width: 90vw;
}

.proDesc span {
  position: relative;
  font-size: 1.7vw;
  font-family: "Poppins", sans-serif;
}

.proDate {
  display: flex;
  flex-direction: column;
  margin: 0 0 3vw 5vw;
  font-size: 1.5vw;
}

.proDate #title {
  font-weight: 100;
  font-family: Poppins, sans-serif, Arial;
}

.proDate #date {
  font-weight: 700;
  font-family: "Inter", sans-serif;
}

.role, .research {
  display: flex;
  flex-direction: column;
  margin-top: 5vw;
}

.role #roleName, #resName {
  font-family: "Poppins", sans-serif;
  font-size: 1.4vw;
  margin-left: 5vw;
}

.role #roleDesc, #resDesc {
  font-size: 1.3vw;
  margin: -2vw 0 0 25vw;
}
